import React from 'react'
import axios from 'axios'

import Link from 'next/link'

import {
    map,
    find,
    filter,
} from 'lodash'

import {
    getGSheetUrl,
    normalizeGSheetJSON,
    GSHEET_SHEET_TASKFORSES,
    GSHEET_SHEET_MEMBERS,
} from '../../config'

import {
    Container,
    Typography,
    Box,
} from '@material-ui/core'

export default function Index({ data }) {
    return (
        <Container maxWidth="xs">
            <Box my={4}>
                <Typography variant="h1" style={{ fontWeight: "bold", textAlign: "center" }}>
                    {`${data["Nome"]}`}
                </Typography>
            </Box>
            <ul>
            {
                map(data.members, d => (
                    <li key={d["Id"]}>
                        <Link href="/members/[Id]" as={`/members/${d["Id"]}`}><a>{d["Nome"]}</a></Link>
                    </li>
                ))
            }
            </ul>
        </Container>
    )
}

export async function getStaticProps({ params }) {

    const taskForse = find(
        normalizeGSheetJSON(await axios.get(getGSheetUrl(GSHEET_SHEET_TASKFORSES))),
        e => e["Id"] === params["Id"]
    )

    const members = filter(
        normalizeGSheetJSON(await axios.get(getGSheetUrl(GSHEET_SHEET_MEMBERS))),
        e => e["Task forse"] === params["Id"]
    )

    return {
        props: {
            data: {
                ...taskForse,
                members,
            },
        },
    }
}

export async function getStaticPaths() {
    return {
        paths: map(
            normalizeGSheetJSON(await axios.get(getGSheetUrl(GSHEET_SHEET_TASKFORSES))),
            e => ({ params: { Id: e["Id"] } })
        ),
        fallback: false,
    }
}
